h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

.link {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

img:last-child {
  margin: 0;
}

button {
  cursor: pointer;
}

body {
  letter-spacing: .03em;
  background: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/41294/hero.jpg") center / cover no-repeat fixed;
  font-family: Roboto, sans-serif;
}

.section {
  width: 100%;
  background: rgba(0, 0, 0, .84);
  margin-top: -8px;
  margin-left: -8px;
  padding: 15px 0;
  position: fixed;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 60px 15px 15px;
}

.search-form {
  justify-content: center;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  flex-basis: calc(25% - 22.5px);
  gap: 30px;
  display: flex;
}

input {
  color: #fff;
  width: 200px;
  text-align: center;
  background: none;
  border: 1px solid #fff;
  border-radius: 3px 0 0 3px;
}

input::-ms-input-placeholder {
  color: #fff;
}

input::placeholder {
  color: #fff;
}

.search-btn {
  width: 100px;
  height: 40px;
  color: #fff;
  background: none;
  border: 1px solid #fff;
  border-radius: 0 3px 3px 0;
}

.thumb {
  height: 180px;
  width: 270px;
}

.thumb > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  display: block;
}

.photo-card {
  border-radius: 0 0 4px 4px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.info {
  color: #fff;
  border-bottom: 1px solid #fff;
  justify-content: space-around;
  gap: 5px;
  padding: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  font-size: 13px;
  display: flex;
}

.load-more.is-hidden {
  display: none;
}

.load-more {
  width: 150px;
  height: 40px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  border-radius: 8px;
  margin: 30px auto;
  display: block;
}

.load-more:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, .2);
}

.submit {
  cursor: pointer;
}

